footer {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
  padding: 25px 0;
  margin-top: auto;
}

footer a {
  color: inherit;
}

a#github > i,
a#linked-in > i {
  font-size: 2rem;
  transition: all 0.4s ease-in-out;
}

a#github:hover > i,
a#linked-in:hover > i {
  transform: rotate(360deg);
}

a#github:visited,
a#thrones-api:visited {
  color: inherit;
}

footer .footer-links {
  display: flex;
  gap: 30px;
}

@media screen and (max-height: 750px) {
  footer {
    padding: 15px 0px;
    gap: 6px;
  }
}
